import React, { createContext, useContext, useState, useEffect } from 'react';
import { useUser, useAuth as useClerkAuth, useClerk } from "@clerk/nextjs";

const AuthContext = createContext(null);

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (!context) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
};

export const AuthProvider = ({ children }) => {
    const { user: clerkUser, isLoaded: userLoaded } = useUser();
    const { signOut } = useClerk();
    const [user, setUser] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (userLoaded) {
            if (clerkUser) {
                // Map Clerk user to your app's user format
                const mappedUser = {
                    _id: clerkUser.id,
                    id: clerkUser.id,
                    name: clerkUser.fullName || clerkUser.username || clerkUser.emailAddresses[0].emailAddress,
                    email: clerkUser.emailAddresses[0].emailAddress,
                    role: clerkUser.publicMetadata.role || { name: 'user' }, // Fallback to user
                    isActive: true
                };
                setUser(mappedUser);
                localStorage.setItem('user', JSON.stringify(mappedUser));
            } else {
                setUser(null);
                localStorage.removeItem('user');
            }
            setLoading(false);
        }
    }, [clerkUser, userLoaded]);

    const login = async () => {
        console.warn('Manual login called while using Clerk. Use Clerk components instead.');
        return { success: false, error: 'Use Clerk for authentication' };
    };

    const register = async () => {
        console.warn('Manual register called while using Clerk. Use Clerk components instead.');
        return { success: false, error: 'Use Clerk for authentication' };
    };

    const logout = async () => {
        await signOut();
        setUser(null);
        localStorage.removeItem('user');
    };

    const value = {
        user,
        loading,
        error: null,
        login,
        register,
        logout,
        isAuthenticated: !!user
    };

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
};
